<template>
  <div class="home-container" v-loading="loading">
    <ul
      class="home-caruse-container"
      :style="{ marginTop }"
      ref="carUseContainer"
      @wheel="hanldeWhell"
      @transitionend="hanldeTransitionEnd"
    >
      <li v-for="item in data" :key="item.id">
        <CaruseItem :data="item" />
      </li>
    </ul>
    <div class="icon icon-up" v-show="index > 0" @click="hanldeRoll(index - 1)">
      <Icon type="arrowUp" />
    </div>
    <div
      class="icon icon-down"
      v-show="index < data.length - 1"
      @click="hanldeRoll(index + 1)"
    >
      <Icon type="arrowDown" />
    </div>
    <ul class="btn">
      <li
        v-for="(item, i) in data"
        :key="item.id"
        :class="{ active: i == index }"
        @click="hanldeRoll(i)"
      ></li>
    </ul>
  </div>
</template>

<script>
import CaruseItem from "@/views/Home/caruseItem.vue";
import Icon from "@/components/Icon";
import { mapState } from "vuex";
export default {
  data() {
    return {
      index: 0,
      caruseItem: 0,
      rolling: false,
    };
  },
  mounted() {
    this.caruseItem = this.$refs.carUseContainer.clientHeight;
    window.addEventListener("resize", this.hanldeSize);
  },
  destroyed() {
    window.removeEventListener("resize", this.hanldeSize);
  },
  created() {
    this.$store.dispatch("banner/fetchBanner");
  },
  components: {
    CaruseItem,
    Icon,
  },
  computed: {
    marginTop() {
      return -this.index * this.caruseItem + "px";
    },
    ...mapState("banner", ["loading", "data"]),
  },
  methods: {
    hanldeRoll(i) {
      this.index = i;
    },
    hanldeWhell(e) {
      if (this.rolling) {
        return;
      }
      if (e.deltaY < -5 && this.index > 0) {
        this.rolling = true;
        this.index--;
      } else if (e.deltaY > 5 && this.index < this.data.length - 1) {
        this.rolling = true;
        this.index++;
      }
    },
    hanldeTransitionEnd() {
      this.rolling = false;
    },
    hanldeSize() {
      this.caruseItem = this.$refs.carUseContainer.clientHeight;
    },
  },
};
</script>

<style scoped lang="less">
.home-container {
  width: 100%;
  height: 100%;
  color: rgb(134, 120, 120);
  position: relative;
  // background: rgb(31, 34, 33);
  overflow: hidden;
  .home-caruse-container {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    transition: 0.5s;
    list-style: none;
    li {
      width: 100%;
      height: 100%;
    }
  }
  .icon {
    position: absolute;
    left: 50%;
    cursor: pointer;
    transform: translate(-50%);
    i {
      font-size: 40px;
    }
  }
  .icon-up {
    top: 20px;
    animation: jumUp 2.5s infinite;
  }
  .icon-down {
    bottom: 20px;
    animation: jumDown 2.5s infinite;
  }
  .btn {
    position: absolute;
    top: 50%;
    left: auto;
    transform: translateY(-50%);
    right: 20px;
    li {
      cursor: pointer;
      display: block;
      width: 8px;
      height: 8px;
      margin-top: 10px;
      border-radius: 50%;
      background: rgb(241, 219, 219);
      border: 1px solid rgb(78, 76, 76);
    }
    .active {
      background: rgb(57, 75, 233);
    }
  }
}
@jump: 15px;
@keyframes jumUp {
  0% {
    transform: translate(-50%, @jump);
    color: rgb(212, 193, 193);
  }
  50% {
    transform: translate(-50%, -@jump);
    color: rgb(105, 97, 97);
  }
  100% {
    transform: translate(-50%, @jump);
    color: rgb(212, 193, 193);
  }
}

@keyframes jumDown {
  0% {
    transform: translate(-50%, -@jump);
    color: rgb(212, 193, 193);
  }
  50% {
    transform: translate(-50%, @jump);
    color: rgb(105, 97, 97);
  }
  100% {
    transform: translate(-50%, -@jump);
    color: rgb(212, 193, 193);
  }
}
</style>
