<template>
  <div
    class="caruse-container"
    ref="container"
    @mousemove="handleMouseMove"
    @mouseleave="handleMouseLeave"
  >
    <div class="image-container" ref="image" :style="imagePosition">
      <ImageLoader :src="data.bigImg" :placeholder="data.midImg" />
    </div>
    <div class="title" ref="title">{{ data.title }}</div>
    <div class="description" ref="description">{{ data.description }}</div>
  </div>
</template>

<script>
import ImageLoader from "@/components/ImageLoader/";
export default {
  props: ["data"],
  components: {
    ImageLoader,
  },
  data() {
    return {
      containerSize: 0,
      innerSize: 0,
      mouseX: 0,
      mouseY: 0,
    };
  },

  computed: {
    imagePosition() {
      if (!this.innerSize || !this.containerSize) {
        return;
      }
      const differenceX = this.innerSize.width - this.containerSize.width;
      const differenceY = this.innerSize.height - this.containerSize.height;
      const left = (-differenceX / this.containerSize.width) * this.mouseX;
      const top = (-differenceY / this.containerSize.height) * this.mouseY;
      return {
        left: left + "px",
        top: top + "px",
      };
    },
  },
  mounted() {
    this.setSize();
    this.handleMouseLeave();
    window.addEventListener("resize", this.setSize);
    this.$refs.description.clientHeight;
    this.$refs.title.style.width = "100%";
    this.$refs.title.style.opacity = 1;
    setTimeout(() => {
      this.$refs.description.style.width = "100%";
      this.$refs.description.style.opacity = 1;
    }, 500);
  },
  destroyed() {
    window.removeEventListener("resize", this.setSize);
  },
  methods: {
    setSize() {
      this.containerSize = {
        width: this.$refs.container.clientWidth,
        height: this.$refs.container.clientHeight,
      };
      this.innerSize = {
        width: this.$refs.image.clientWidth,
        height: this.$refs.image.clientHeight,
      };
    },
    handleMouseMove(e) {
      const rect = this.$refs.container.getBoundingClientRect();
      this.mouseX = e.clientX - rect.left;
      this.mouseY = e.clientY - rect.top;
    },
    handleMouseLeave() {
      this.mouseX = this.containerSize.width / 2;
      this.mouseY = this.containerSize.height / 2;
    },
  },
};
</script>

<style scoped lang="less">
.caruse-container {
  width: 100%;
  height: 100%;
  overflow: hidden;
  position: relative;
  .image-container {
    width: 110%;
    height: 110%;
    position: absolute;
    overflow: hidden;
    transition: 0.2s;
    cursor: pointer;
  }

  .title,
  .description {
    color: rgb(199, 191, 191);
    position: absolute;
    font-size: 30px;
    white-space: nowrap;
    overflow: hidden;
    width: 0;
    opacity: 0;
    transition: 3s;
    text-shadow: 1px 1px 1px rgb(200, 127, 127);
  }
  .description {
    top: 65%;
    left: 50px;
  }
  .title {
    top: 60%;
    left: 50px;
  }
}
</style>
