<template>
  <div class="imageloader-container">
    <img class="placeholder" v-if="!isShow" :src="placeholder" alt="" />
    <img
      class="origin"
      :src="src"
      alt=""
      @load="hanldeLoading"
      :style="{ opacity: isCompleted, transition: `${duration}ms` }"
    />
  </div>
  <!-- 这个组件其实很简单，有两个img图片，一个是原图，一个用于暂位，当原图加载完成之后，暂位图片remove-->
</template>

<script>
export default {
  data() {
    return {
      isLoading: false,
      //原图加载完成之后位true，并将opacity设置1，
      isShow: false,
      //原图加载完成之后为ture，并将暂位图remove
    };
  },
  props: {
    src: {
      type: String,
      required: true,
    },
    placeholder: {
      type: String,
      required: true,
    },
    duration: {
      type: Number,
      default: 500,
    },
  },
  computed: {
    isCompleted() {
      return this.isLoading ? 1 : 0;
      //该计算属性依赖isLoading，为真，opacity = 1；否则 = 0；
    },
  },
  methods: {
    hanldeLoading() {
      //当原图加载完成之后触发该事件
      this.isLoading = true;
      //只要原图一加载完成，就将isLoading状态设为true；
      setTimeout(() => {
        this.isShow = true;
        //当isShow状态设为ture之后，暂位图立即删除，但原图孩子啊缓慢的显示出来，所以这时候页面就会有空，
        //所以需要当原图完全显示出来之后在将状态设未true；
        this.$emit("load");
        //抛出事件!
      }, this.duration);
    },
  },
};
</script>

<style scoped lang="less">
.imageloader-container {
  overflow: hidden;
  margin: 0 auto;
  position: relative;
  width: 100%;
  height: 100%;
  img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    object-fit: cover;
    height: 100%;
  }
  .placeholder {
    filter: blur(2vw);
  }
}
</style>
